///////////////////////////////////////////////////////////////////////////////
import React, { useEffect, useState } from "react";
import CheckBox from "./CheckBox";
import Graph from "./Graph";
import handleCheckClick from "./handleClickCheck";
import fetchData from './api_req';
///////////////////////////////////////////////////////////////////////////////
//
//URL          :fetchDataURL;
//apiUrl            :resasAPI;
//api_population    :prefCode;
//
///////////////////////////////////////////////////////////////////////////////
const apiUrl = 'https://opendata.resas-portal.go.jp/api/v1/prefectures';                                            //APIURL
const api_population = "https://opendata.resas-portal.go.jp/api/v1/population/composition/perYear?prefCode=";       //URL
///////////////////////////////////////////////////////////////////////////////
//
//CSS
//     
///////////////////////////////////////////////////////////////////////////////
const Styles: { [key: string]: React.CSSProperties } = {
    label: {
        fontSize:"25px",
        padding: "0.5rem 2rem",
        borderLeft: "4px solid #000",
        marginLeft: "10px",
    },
    label1: {
        fontSize:"10px",
        marginLeft: "10px",
    },
    checkerea: {
        height: "300px",
        overflow: "auto",
        border: "4px solid #000",
        marginLeft: "10px",
    },
    select: {
        fontSize: "20px",
        height: "40px",
        width: "150px",
    },
};
///////////////////////////////////////////////////////////////////////////////
//
//Main
//fetchData    setPrefectures{message: null;result: {prefCode: number;prefName: string;}[]}
//
//
//handleCheckClick                            checkBox
//                                                                            
//          api_population + String(prefCode)     :URL              
//          selectedData                          :select, 
//          prefPopulation                        :
//          setPrefPopulation                     :set
//          prefName                                  
//          prefCode                              :    
//          check                                 :         
//
//
//handleDataChange                          select
//
//
//Graph                                     Graph.tsx
//          prefName                        prefPopulationselect
//          data[year, data]                label
///////////////////////////////////////////////////////////////////////////////
const Main = () => {
    /*==============================================================================*/
    //      prefectures, setPrefecturesprefPopulation, setPrefPopulation
    /*==============================================================================*/
    const [prefectures, setPrefectures] = useState<{
        message: null;
        result: {
            prefCode: number;               /**/
            prefName: string;               /**/
        }[] | null;
    } | null>(null);

    const [prefPopulation, setPrefPopulation] = useState<
        { 
            prefName: string;                /**/
            data: { label: string;           /**/
                   data: { year: number;     /**/
                          value: number;     /**/
                         }[]; }[] }[]
    >([]);
    const [selectedData, setSelectedData] = useState("");         /*selectedData*/
    /*==============================================================================*/
    //      fetchData
    /*==============================================================================*/
    useEffect(() => {
        fetchData(apiUrl)
            .then((results) => {
                setPrefectures(results);
            })
            .catch((error) => { });
    }, []);
    /*==============================================================================*/
    //      checkbox
    /*==============================================================================*/
    const handleCheck = (prefName: string, prefCode: number, check: boolean) => {
        handleCheckClick(api_population + String(prefCode), selectedData, prefPopulation, setPrefPopulation, prefName, prefCode, check);
    };
    /*==============================================================================*/
    //       select
    /*==============================================================================*/
    const handleDataChange = (event: React.ChangeEvent<HTMLSelectElement>) => {
        const selectedValue = event.target.value;
        setSelectedData(selectedValue);
    };
    /*==============================================================================*/
    //
    /*==============================================================================*/
    return (
        <main>
            <div>
                <h1 style={Styles.label}>Resas_APIweb</h1>
                <h3 style={Styles.label1}>checkbox</h3>
                <h3 style={Styles.label1}>select</h3>
                {prefectures && (
                    <div style={Styles.checkerea}>
                        <CheckBox
                            prefectures={prefectures.result || []}
                            onChange={handleCheck}
                        />
                    </div>
                )}
            </div>
            <div>
                <label>
                    <select value={selectedData} onChange={handleDataChange} style={Styles.select}>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                    </select>
                </label>
            </div>
            <div>
                {selectedData === "" ? (
                    <Graph populationdata={prefPopulation.map((pref) => ({
                        prefName: pref.prefName,
                        data: (pref.data.find((item) => item.label === "")?.data) || []
                    }))} />
                ) : null}

                {selectedData === "" ? (
                    <Graph populationdata={prefPopulation.map((pref) => ({
                        prefName: pref.prefName,
                        data: (pref.data.find((item) => item.label === "")?.data) || []
                    }))} />
                ) : null}


                {selectedData === "" ? (
                    <Graph populationdata={prefPopulation.map((pref) => ({
                        prefName: pref.prefName,
                        data: (pref.data.find((item) => item.label === "")?.data) || []
                    }))} />
                ) : null}


                {selectedData === "" ? (
                    <Graph populationdata={prefPopulation.map((pref) => ({
                        prefName: pref.prefName,
                        data: (pref.data.find((item) => item.label === "")?.data) || []
                    }))} />
                ) : null}

            </div>
        </main>
    );
};
///////////////////////////////////////////////////////////////////////////////
//
//
//
///////////////////////////////////////////////////////////////////////////////
export default Main;
///////////////////////////////////////////////////////////////////////////////
//
//EOF
